`, "",{"version":3,"sources":["webpack://./src/js/components/Create/CreateDlg.vue"],"names":[],"mappings":";AAiIA;CACA,8CAAA;CACA,iBAAA;AACA;AAEA;CACA,aAAA;CACA,8BAAA;AACA","sourcesContent":["<!--\n  - @copyright Copyright (c) 2018 René Gieling <github@dartcafe.de>\n  -\n  - @author René Gieling <github@dartcafe.de>\n  -\n  - @license GNU AGPL version 3 or any later version\n  -\n  - This program is free software: you can redistribute it and/or modify\n  - it under the terms of the GNU Affero General Public License as\n  - published by the Free Software Foundation, either version 3 of the\n  - License, or (at your option) any later version.\n  -\n  - This program is distributed in the hope that it will be useful,\n  - but WITHOUT ANY WARRANTY; without even the implied warranty of\n  - MERCHANTABILITY or FITNESS FOR A PARTICULAR PURPOSE.  See the\n  - GNU Affero General Public License for more details.\n  -\n  - You should have received a copy of the GNU Affero General Public License\n  - along with this program.  If not, see <http://www.gnu.org/licenses/>.\n  -\n  -->\n\n<template lang=\"html\">\n\t<div class=\"create-dialog\">\n\t\t<ConfigBox :title=\"t('polls', 'Title')\">\n\t\t\t<template #icon>\n\t\t\t\t<SpeakerIcon />\n\t\t\t</template>\n\t\t\t<InputDiv ref=\"pollTitle\"\n\t\t\t\tv-model=\"title\"\n\t\t\t\ttype=\"text\"\n\t\t\t\t:placeholder=\"t('polls', 'Enter Title')\"\n\t\t\t\t@submit=\"confirm\" />\n\t\t</ConfigBox>\n\n\t\t<ConfigBox :title=\"t('polls', 'Poll type')\">\n\t\t\t<template #icon>\n\t\t\t\t<CheckIcon />\n\t\t\t</template>\n\t\t\t<RadioGroupDiv v-model=\"pollType\" :options=\"pollTypeOptions\" />\n\t\t</ConfigBox>\n\n\t\t<div class=\"create-buttons\">\n\t\t\t<NcButton @click=\"cancel\">\n\t\t\t\t<template #default>\n\t\t\t\t\t{{ t('polls', 'Cancel') }}\n\t\t\t\t</template>\n\t\t\t</NcButton>\n\t\t\t<NcButton :disabled=\"titleEmpty\"\n\t\t\t\ttype=\"primary\"\n\t\t\t\t@click=\"confirm\">\n\t\t\t\t<template #default>\n\t\t\t\t\t{{ t('polls', 'Apply') }}\n\t\t\t\t</template>\n\t\t\t</NcButton>\n\t\t</div>\n\t</div>\n</template>\n\n<script>\nimport { mapState } from 'vuex'\nimport { showSuccess, showError } from '@nextcloud/dialogs'\nimport { NcButton } from '@nextcloud/vue'\nimport ConfigBox from '../Base/ConfigBox.vue'\nimport RadioGroupDiv from '../Base/RadioGroupDiv.vue'\nimport SpeakerIcon from 'vue-material-design-icons/Bullhorn.vue'\nimport CheckIcon from 'vue-material-design-icons/Check.vue'\nimport InputDiv from '../Base/InputDiv.vue'\n\nexport default {\n\tname: 'CreateDlg',\n\n\tcomponents: {\n\t\tNcButton,\n\t\tSpeakerIcon,\n\t\tCheckIcon,\n\t\tConfigBox,\n\t\tRadioGroupDiv,\n\t\tInputDiv,\n\t},\n\n\tdata() {\n\t\treturn {\n\t\t\tpollType: 'datePoll',\n\t\t\ttitle: '',\n\t\t\tpollTypeOptions: [\n\t\t\t\t{ value: 'datePoll', label: t('polls', 'Date poll') },\n\t\t\t\t{ value: 'textPoll', label: t('polls', 'Text poll') },\n\t\t\t],\n\t\t}\n\t},\n\n\tcomputed: {\n\t\t...mapState({\n\t\t\tpoll: (state) => state.poll,\n\t\t}),\n\n\t\ttitleEmpty() {\n\t\t\treturn this.title === ''\n\t\t},\n\t},\n\n\tmethods: {\n\t\t/** @public */\n\t\tsetFocus() {\n\t\t\tthis.$refs.pollTitle.setFocus()\n\t\t},\n\n\t\tcancel() {\n\t\t\tthis.title = ''\n\t\t\tthis.pollType = 'datePoll'\n\t\t\tthis.$emit('close-create')\n\t\t},\n\n\t\tasync confirm() {\n\t\t\ttry {\n\t\t\t\tconst response = await this.$store.dispatch('poll/add', { title: this.title, type: this.pollType })\n\t\t\t\tthis.cancel()\n\t\t\t\tshowSuccess(t('polls', 'Poll \"{pollTitle}\" added', { pollTitle: response.data.title }))\n\t\t\t\tthis.$router.push({ name: 'vote', params: { id: response.data.id } })\n\t\t\t} catch {\n\t\t\t\tshowError(t('polls', 'Error while creating Poll \"{pollTitle}\"', { pollTitle: this.title }))\n\t\t\t}\n\t\t},\n\t},\n}\n</script>\n\n<style lang=\"css\">\n.create-dialog {\n\tbackground-color: var(--color-main-background);\n\tpadding: 8px 20px;\n}\n\n.create-buttons {\n\tdisplay: flex;\n\tjustify-content: space-between;\n}\n</style>\n"],"sourceRoot":""}]);
